<template>
<v-layout column fill-height>
  <v-layout align-start fill-height>
    <v-flex xs10 fill-height ref="boxChatMess">
      <v-toolbar flat dense class="cyan" dark>
        <v-toolbar-title>Чат ВСГУТУ</v-toolbar-title>
      </v-toolbar>
      <messages-panel/>
    </v-flex>
    <v-flex xs2 ml-2 class="elevation-10">
      <v-toolbar flat dense class="cyan" dark>
        <v-toolbar-title>Онлайн</v-toolbar-title>
      </v-toolbar>
      <v-flex>
        <v-text-field v-model="curentUser.name"></v-text-field>
      </v-flex>
    </v-flex>
  </v-layout>

  <v-layout>
    <v-flex>
      <v-text-field
            v-model="newMessage"
            @keyup.enter="sendMassage"
            label="Сообщение..."
            single-line
            outline></v-text-field>
    </v-flex>
    <v-btn @click="sendMassage">Отправить</v-btn>
  </v-layout>

  </v-layout>
</template>

<script>
import io from 'socket.io-client'
import MessagesPanel from '@/components/Messages'

export default {
  components: {
    MessagesPanel
  },
  mounted () {
    this.$root.$emit('setMm', this.$refs.boxChatMess.clientHeight)

    this.socket.on('newMessage', (data) => {
      this.$root.$emit('newMessage', {text: data.message, name: data.name, type: 0})
    })
  },

  data () {
    return {
      curentUser: {name: Math.random()},
      newMessage: null,
      socket: io('192.168.0.103:3000')
    }
  },
  methods: {
    sendMassage () {
      if (!this.newMessage) return
      this.$root.$emit('newMessage', {text: this.newMessage, name: this.curentUser.name, type: 1})

      this.socket.emit('newMessage', {
        message: this.newMessage,
        name: this.curentUser.name
      })

      this.newMessage = null
    }
  }
}
</script>

<style scoped>
</style>



